html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-dollar {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

.icon-home {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

.icon-user {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

html {
  font-size: 12px;
}

body {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.2;
}

.ws-items {
  margin: 0 auto;
  max-width: 680px;
}

.ws-items__row {
  font-size: 0;
}

.ws-items__col {
  display: inline-block;
  vertical-align: top;
  padding: 10px;
  width: 33.33%;
}

.ws-item {
  position: relative;
  display: block;
  padding: 2px;
  border-radius: 3px;
  color: #fff;
  background-image: linear-gradient(to top, #3a4456 0%, #3e495e 29.3%, #45516f 60%, #3e495f 100%);
  letter-spacing: 0.01em;
  text-align: center;
  text-decoration: none;
}

.ws-item.is-active {
  color: #000;
  background-image: linear-gradient(to top, #618957 0%, #9cbc6c 29.3%, #9cbb71 100%);
}

.ws-item:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid #fff;
  border-radius: 3px;
  pointer-events: none;
}

.ws-item__header {
  position: relative;
  padding: 5px 25px;
  text-align: center;
}

.ws-item__icon {
  position: absolute;
  top: 5px;
  left: 10px;
  fill: #fff;
  color: #3C465B;
  font-size: 1.2rem;
}

.ws-item__title {
  font-size: 1rem;
}

.ws-item__price {
  padding: 3px 0 5px;
  font-size: 1.375rem;
  line-height: 1;
}

.ws-item__label, .ws-item__total {
  display: inline-block;
  vertical-align: top;
}

.ws-item__label {
  padding: 5px 5px 1px 0;
  font-size: 0.775rem;
}

.ws-item__total {
  font-size: 1.208rem;
}

.ws-item__footer {
  padding-bottom: 3px;
  font-size: 0;
}

.ws-item__goal, .ws-item__rtg {
  display: inline-block;
  vertical-align: top;
  width: 50%;
}
